<template>
  <div class="app-container">
    <!-- Header -->
    <el-header class="app-header">
      <div class="header-content">
        <h1>书法识别系统</h1>
        <div class="header-right">
          <el-button type="text">关于</el-button>
          <el-button type="text">帮助</el-button>
        </div>
      </div>
    </el-header>

    <!-- Main Content -->
    <el-main class="main-content">
      <el-row :gutter="20" class="content-row">
        <!-- Upload Section -->
        <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
          <el-card class="upload-card" shadow="hover">
            <template #header>
              <div class="card-header">
                <span>上传图片</span>
                <el-tag size="small" type="info">支持jpg、png格式</el-tag>
              </div>
            </template>
            
            <div class="upload-area">
              <el-upload
                :action="YOUR_UPLOAD_URL"
                accept="image/*"
                @change="onFileChange"
                :show-file-list="false"
                class="upload-component"
                drag
              >
                <template #default>
                  <div class="upload-trigger" :class="{ 'has-image': imageUrl }">
                    <el-icon v-if="!imageUrl"><Upload /></el-icon>
                    <div v-if="!imageUrl" class="upload-text">
                      <span>点击或拖拽图片到此处</span>
                      <span class="upload-hint">支持单个图片上传</span>
                    </div>
                    <el-image v-if="imageUrl" :src="imageUrl" fit="contain" class="preview-image"></el-image>
                  </div>
                </template>
              </el-upload>
              
              <el-button type="primary" @click="submitImage" class="submit-button" :loading="loading">
                {{ loading ? '识别中...' : '开始识别' }}
              </el-button>
            </div>
          </el-card>
        </el-col>

        <!-- Result Section -->
        <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
          <el-card class="result-card" shadow="hover">
            <template #header>
              <div class="card-header">
                <span>识别结果</span>
                <el-tag size="small" type="success" v-if="result && !loading">识别完成</el-tag>
                <el-tag size="small" type="warning" v-if="loading">处理中</el-tag>
                <el-tag size="small" type="info" v-if="!result && !loading">等待上传</el-tag>
              </div>
            </template>
            
            <div class="result-content">
              <el-skeleton :rows="3" animated v-if="loading"/>
              <div v-else-if="result" class="result-text">
                {{ result }}
              </div>
              <div v-else class="empty-result">
                <el-icon class="empty-icon"><Picture /></el-icon>
                <p>请上传一张书法图片开始识别</p>
                <p class="empty-hint">支持常见的图片格式，如 JPG、PNG 等</p>
              </div>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </el-main>

    <!-- Footer -->
    <el-footer class="app-footer">
      <p> 2025 书法识别系统 - All Rights Reserved</p>
    </el-footer>
  </div>
</template>

<script>
import { Upload, Picture } from '@element-plus/icons-vue'

export default {
  components: {
    Upload,
    Picture
  },
  data() {
    return {
      imageUrl: null,
      result: null,
      loading: false,
      selectedFile: null,
    };
  },
  methods: {
    onFileChange(file) {
      if (file) {
        this.selectedFile = file.raw;
        this.imageUrl = URL.createObjectURL(file.raw);
      }
    },
    async submitImage() {
      if (!this.selectedFile) {
        this.$message.warning('请先选择一张图片');
        return;
      }
      this.loading = true;
      const formData = new FormData();
      formData.append('image', this.selectedFile);
    
      try {
        console.log('Sending request to:', '/api');
        const response = await fetch('/api', {
          method: 'POST',
          body: formData,
        });
        console.log('Response status:', response.status);
        console.log('Response status text:', response.statusText);
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        this.result = await response.json();
        console.log('Response data:', this.result);
        this.$message.success('识别成功！');
      } catch (error) {
        console.error('Error details:', {
          message: error.message,
          error: error
        });
        this.result = '识别失败: ' + error.message;
        this.$message.error('识别失败，请重试');
      } finally {
        this.loading = false;
      }
    },
  },
};
</script>

